    position:static;
    z-index: 200;
    padding:0;
    overflow: hidden;

}

.contactForm {
    font-family: futura-pt, sans-serif;
    font-weight: 400;
    font-style: normal;
  
}

.myLabel {
    margin-left:40px;
    font-size:28px;
    margin-bottom:4px;
    display:block;
    margin-top:32px;
    text-transform: uppercase;
    opacity:.5;
    position: relative;
}
.contactForm label {
    margin-left:40px;
    font-size:28px;
    margin-bottom:4px;
    display:block;
    margin-top:32px;
    text-transform: uppercase;
    opacity:.5;
}
.contactForm input {
    display:block;
    width:calc(100% - 80px);
    margin-left:40px;
    margin-right:40px;
    padding:14px;
    box-sizing: border-box;
    background-color: rgb(61, 68, 82);
    border:none;
    outline:none;
    font-size:24px;
    font-weight: 600;
    padding-left:0px;
    outline-color:black;
    border-bottom:4px solid rgba(255,255,255,.25);
    cursor: pointer;
    color:rgba(255,255,255,.6);
}

.contactForm input:active {
    background-color: rgb(61, 68, 82);
    border-bottom:4px solid #7ebcd4;
    font-size:28px;
    color:white;
}

.contactForm input:focus {
    background-color: rgb(61, 68, 82);
    border-bottom:4px solid #7ebcd4;
    font-size:28px;
    color:white;
}

.contactForm input:visited {
    font-size:20px;
    color:rgba(255,255,255,.2);
}

.contactForm input[type=email] {
    background-color: rgb(61, 68, 82);
}

.contactForm input[type=email]:active {
    background-color: rgb(61, 68, 82);

}
.contactForm input[type=email]:focus {
    background-color: rgb(61, 68, 82);
}

.contactForm input[type=email] :-internal-autofill-selected {
    background-color: rgb(61, 68, 82) !important;
    
}

.contactForm input:-internal-autofill-selected  {
    color:none;
}

input:-internal-autofill-selected {
    background-color: rgb(61, 68, 82) !important;
}

/* input:-autofill {
